body .site-search .widget_product_search form::before {
  content: "";
  height: 100%;
  background-image: url("../image/search.svg");
  content: "";
  background-repeat: no-repeat;
  background-size: 15px 15px;
  background-position: center center;
  aspect-ratio: 1/1;
  top: 0px;
  left: 0px;
}
body .site-search .widget_product_search form input[type=search] {
  border-radius: 30px;
  height: 40px;
}

.visually-hidden:not(:focus):not(:active) {
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

:root {
  --orange: #FF5F01;
  --green: rgb(19, 192, 91);
  --red: red;
  --grey: #979797;
  --grey-l: #cdcdcd;
  --grey-ll: #e7e7e7;
  --white: #fffcfc;
  --black: #262626;
}

@font-face {
  font-family: "avegaitalic";
  src: url("../fonts/avega-italic-webfont.woff2") format("woff2"), url("../fonts/avega-italic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}
/**
 * Fontfaces
 * When you are preloading fonts make sure that what is preloaded matches with the src defined in your
 * @font-face rules
 * https://nooshu.github.io/blog/2021/01/23/the-importance-of-font-face-source-order-when-used-with-preload/
 */
/*@include declare-font-face("Libre Franklin", "../fonts/LibreFranklin-Regular");
@include declare-font-face("Libre Franklin", "../fonts/LibreFranklin-Bold", 700);
@include declare-font-face("Great Vibes", "../fonts/GreatVibes-Regular", 500);*/
:root :where(body) {
  font-family: "Merriweather Sans", sans-serif !important;
}

:root :where(a:where(:not(.wp-element-button))) {
  text-decoration: none !important;
}

body {
  font-family: "Merriweather Sans", sans-serif !important;
  font-size: 16px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 h4,
.h4 {
  font-family: "avegaitalic", sans-serif;
  font-weight: 500;
  text-align: center;
  color: #262626;
}

.category-title {
  text-transform: uppercase;
  color: #1C91F9;
  font-size: 2.2em;
}

.italic {
  font-style: italic;
  font-size: 12px;
}

.status.Enattente, .status.Encours, .status.Attentepaiement {
  font-style: italic;
  color: var(--orange);
}
.status.Termine {
  color: var(--green);
}
.status.Rembourse {
  color: var(--green);
  font-style: italic;
}
.status.Annule, .status.choue {
  color: var(--red);
}

.woocommerce-checkout button.wc-block-components-button {
  border-radius: 15px;
  background-color: #1C91F9;
  font-family: "avegaitalic", sans-serif;
}
.woocommerce-checkout .wp-block-woocommerce-checkout-order-summary-block {
  font-family: "avegaitalic", sans-serif;
}
.woocommerce-checkout .wp-block-woocommerce-checkout-order-summary-block .wc-block-components-product-name {
  font-family: "Merriweather Sans", sans-serif;
}
.woocommerce-checkout .wp-block-woocommerce-checkout-order-summary-block .wc-block-components-product-metadata {
  display: none;
}

.woocommerce-cart .wp-block-woocommerce-empty-cart-block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.woocommerce-cart .wp-block-woocommerce-empty-cart-block .wc-block-cart__empty-cart__title.with-empty-cart-icon::before {
  display: none;
}
.woocommerce-cart .wp-block-woocommerce-empty-cart-block p {
  margin: 20px 0px;
}
.woocommerce-cart .wp-block-woocommerce-empty-cart-block p.has-text-align-center a {
  background-color: #F6CC67;
  border: 1px solid #262626;
  padding: 5px 20px;
  border-radius: 30px;
  font-family: "avegaitalic", sans-serif;
  color: #FFFFFF;
  transition: all 0.2s ease-in-out;
}
.woocommerce-cart .wp-block-woocommerce-empty-cart-block p.has-text-align-center a:hover {
  letter-spacing: 1px;
}

footer {
  display: flex;
  background-color: #262626;
  border-radius: 30px;
  padding: 60px 30px 20px;
  color: #FFFFFF;
  flex-wrap: wrap;
  width: 100%;
  text-transform: uppercase;
  gap: 30px;
  background-image: url("../image/footer_bg.svg");
  background-size: auto 90%;
  background-repeat: no-repeat;
  background-position: bottom left;
  justify-content: center;
}
@media screen and (min-width: 75rem) {
  footer {
    justify-content: flex-start;
    padding: 60px 70px 20px;
  }
}
footer .left {
  display: flex;
  width: 100%;
  gap: 20px;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media screen and (min-width: 75rem) {
  footer .left {
    text-align: left;
    width: 50%;
    align-items: flex-start;
  }
}
footer .left div {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: center;
  width: 100%;
}
@media screen and (min-width: 75rem) {
  footer .left div {
    width: 100%;
    align-items: flex-start;
  }
}
footer .left .search {
  width: fit-content;
}
footer .left .info {
  font-size: 1em;
}
footer .left .big {
  font-size: 1.2em;
}
footer .left .tylt-button {
  width: 70%;
  min-width: 300px;
}
@media screen and (min-width: 75rem) {
  footer .left .tylt-button {
    width: 350px;
  }
}
footer .right {
  border-left: none;
  width: auto;
}
@media screen and (min-width: 75rem) {
  footer .right {
    border-left: 3px solid #FFFFFF;
  }
}
footer .right nav {
  height: 100%;
}
footer .right nav ul {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5px;
}
footer .right nav ul li {
  text-align: center;
  margin-left: 0px;
}
@media screen and (min-width: 75rem) {
  footer .right nav ul li {
    text-align: left;
    margin-left: 20px;
  }
}
footer .right nav ul li a,
footer .right nav ul li a:visited {
  color: #FFFFFF;
}
footer .under {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}
footer .under .socials {
  display: flex;
  justify-content: center;
  flex-direction: row;
  gap: 15px;
}
footer .under .socials a {
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}
footer .under .socials a:hover {
  rotate: 15deg;
}
footer .under .socials img {
  width: 25px;
  aspect-ratio: 1/1;
}
footer .under .cpr {
  width: 100%;
  text-align: center;
  justify-content: center;
  font-size: 12px;
}
footer .under .cpr a,
footer .under .cpr a:visited {
  color: #FFFFFF;
}

.tylt_card_product {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 5% 5% 10px 5%;
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
  position: relative;
}
.tylt_card_product .tylt_tags {
  position: absolute;
  top: -10px;
  left: -10px;
  color: #FFFFFF;
  display: flex;
  gap: 5px;
  rotate: -2deg;
  flex-direction: column;
  font-family: "avegaitalic", sans-serif;
  z-index: 11;
}
.tylt_card_product .tylt_tags .tylt_featured_product {
  text-transform: uppercase;
  background-color: #F77B0D;
  padding: 5px 20px;
  border-radius: 30px;
  color: #FFFFFF;
  text-align: center;
}
.tylt_card_product:hover .tylt_card_image img {
  scale: 1.1;
  rotate: 2deg;
}
.tylt_card_product.out-of-stock div, .tylt_card_product.out-of-stock h3 {
  opacity: 0.5;
}
.tylt_card_product .tylt_card_image {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 20px;
  overflow: hidden;
}
.tylt_card_product .tylt_card_image .empty {
  display: flex;
  background: radial-gradient(circle at 20% 30%, #1C91F9, transparent 90%), radial-gradient(circle at 80% 70%, #F77B0D, transparent 90%), #4b4b4b;
  background-blend-mode: screen;
  width: 100%;
  height: 100%;
  text-transform: uppercase;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  font-size: 20px;
  text-align: center;
}
.tylt_card_product .tylt_card_image img {
  transition: all 0.2s ease-in-out;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.tylt_card_product .tylt_card_name {
  margin-top: 10px;
  font-size: 0.8em;
  color: #262626;
  min-height: 60px;
  font-family: "Merriweather Sans", sans-serif;
  text-transform: uppercase;
  font-style: italic;
  font-weight: 900;
}
.tylt_card_product .price,
.tylt_card_product .out-of-stock-message {
  display: flex;
  align-items: center;
  margin: auto;
  font-size: 1.8em;
  margin-bottom: 6px;
  min-height: 50px;
  justify-content: center;
  font-family: "avegaitalic", sans-serif;
}
.tylt_card_product .price bdi,
.tylt_card_product .out-of-stock-message bdi {
  color: #1C91F9;
}
.tylt_card_product .price del,
.tylt_card_product .out-of-stock-message del {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tylt_card_product .price del > span,
.tylt_card_product .out-of-stock-message del > span {
  height: 23px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tylt_card_product .price del > span bdi,
.tylt_card_product .out-of-stock-message del > span bdi {
  color: rgba(38, 38, 38, 0.5);
  font-size: 0.6em;
  text-decoration: line-through;
  position: relative;
  top: 0.1em;
}
.tylt_card_product .price ins bdi,
.tylt_card_product .out-of-stock-message ins bdi {
  color: #f9963e;
}
.tylt_card_product .out-of-stock-message {
  color: black;
  font-size: 1em;
  letter-spacing: 2px;
}
.tylt_card_product .tylt_card_actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: "avegaitalic", sans-serif;
  gap: 5px;
}
.tylt_card_product .tylt_card_actions .tylt-see-more {
  color: #F77B0D;
  font-size: 12px;
  transition: all 0.2s ease-in-out;
}
.tylt_card_product .tylt_card_actions .tylt-see-more:hover {
  letter-spacing: 1px;
}
.tylt_card_product .tylt_card_actions a.tylt_card_add_to_cart {
  display: flex;
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  background-color: #1C91F9;
  color: #FFFFFF;
  font-size: 17px;
  border-radius: 30px;
  padding: 2px 24px;
  border: 1px solid #262626;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
  position: relative;
  top: -2px;
}
.tylt_card_product .tylt_card_actions a.tylt_card_add_to_cart:hover {
  letter-spacing: 1px;
}

.woocommerce-checkout .entry-content div[data-shortcode=checkout],
.woocommerce-checkout .entry-content .woocommerce {
  max-width: 1400px !important;
  margin: auto;
}
.woocommerce-checkout .entry-content div[data-shortcode=checkout] label,
.woocommerce-checkout .entry-content div[data-shortcode=checkout] p,
.woocommerce-checkout .entry-content .woocommerce label,
.woocommerce-checkout .entry-content .woocommerce p {
  margin-top: 20px;
}
.woocommerce-checkout .entry-content div[data-shortcode=checkout] h3,
.woocommerce-checkout .entry-content .woocommerce h3 {
  text-align: left;
}
.woocommerce-checkout .entry-content div[data-shortcode=checkout] a,
.woocommerce-checkout .entry-content div[data-shortcode=checkout] a:visited,
.woocommerce-checkout .entry-content .woocommerce a,
.woocommerce-checkout .entry-content .woocommerce a:visited {
  color: #1C91F9;
}

body .formulaire {
